import React from 'react';
import { useState } from 'react';
import { useEffect } from 'react';
import classes from './SingleTown.module.css';
import firebase from '../firebase';

function SingleTown({ propsCity }) {

  const api = {
    base: "https://api.openweathermap.org/data/2.5/",
    key: "26f7c14de162ddf380af26c56863bd3a"
  }

  const [city, setCity] = useState({});

  useEffect(() => {
    fetch(`${api.base}weather?q=${propsCity.name}&units=metric&lang=hr&appid=${api.key}`)
      .then(response => response.json())
      .then(result => {
        result = { ...result, realId: propsCity.id }
        setCity(result)
      })
  }, [api.base, propsCity, api.key])

  const handleRemoveCity = () => {
    const db = firebase.firestore();
    db.collection('cities').doc(city.realId).delete()
  }

  return (
    <>
      {
         city.main ?
          < div className={classes.weatherResults} >
            <div className={classes.locationBox}>
              {city.name}, {city.sys.country}
            </div>
            <div className={classes.weatherBox}>
              <img src={`http://openweathermap.org/img/wn/${city.weather[0].icon}@2x.png`} alt="" />
              <div className={classes.weatherBoxInfo}>
                <div className={classes.weather}>{city.weather[0].description}</div>
                <div className={classes.temp}>{Math.round(city.main.temp)} °C</div>
                <div className={classes.realFeel}>Dojam: {Math.round(city.main.feels_like)} °C</div>
                <div className={classes.realFeel}>Vjetar: {Math.round(city.wind.speed * 3.6)} km/h</div>
              </div>
            </div>
            <button onClick={handleRemoveCity} className={classes.unfollowBtn}>Prestani pratit</button>
          </div >
          :
          <h1>Loading...</h1>
      }



    </>
  )
}

export default SingleTown;

