import React, { useEffect, useState } from 'react'
import { useParams, useHistory } from "react-router-dom";
import classes from './ResultsComponent.module.css'
import firebase from '../firebase';

function ResultsComponent() {
  const api = {
    base: "https://api.openweathermap.org/data/2.5/",
    key: "26f7c14de162ddf380af26c56863bd3a"
  }

  const [weather, setWeather] = useState({});

  let { id } = useParams();

  useEffect(() => {
    fetch(`${api.base}weather?q=${id.trim()}&units=metric&lang=hr&appid=${api.key}`)
      .then(response => response.json())
      .then(result => {
        setWeather(result)
      })
  }, [id, api.base, api.key])

  const saveCityToFirestore = (e) => {
    e.preventDefault();
    const db = firebase.firestore()
    db.collection('cities').add({ name: id })
    history.push('/');
  }

  let history = useHistory();

  const returnHome = () => {
    history.push('/');
  }

  return (
    <React.Fragment>
      {
        weather.main ?
          <>
            <div className={classes.weatherResults}>
              <div className={classes.locationBox}>
                {weather.name}, {weather.sys.country}
              </div>
              <div className={classes.weatherBox}>
                <img src={`http://openweathermap.org/img/wn/${weather.weather[0].icon}@2x.png`} alt="" />
                <div className={classes.weatherBoxInfo}>
                  <div className={classes.weather}>{weather.weather[0].description}</div>
                  <div className={classes.temp}>{Math.round(weather.main.temp)} °C</div>
                  <div className={classes.realFeel}>Dojam: {Math.round(weather.main.feels_like)} °C</div>
                  <div className={classes.realFeel}>Vjetar: {Math.round(weather.wind.speed * 3.6)} km/h</div>
                  <div className={classes.realFeel}>Minimalno: {Math.round(weather.main.temp_min)} °C</div>
                  <div className={classes.realFeel}>Maksimalno: {Math.round(weather.main.temp_max)} °C</div>
                  <div className={classes.realFeel}>Tlak zraka: {Math.round(weather.main.pressure)} hPa</div>
                  <div className={classes.realFeel}>Vlaga: {Math.round(weather.main.humidity)} %</div>
                </div>
              </div>
              <button className={classes.followCity} onClick={saveCityToFirestore}>Prati grad</button>
            </div>
            <button className={classes.returnBtn} onClick={returnHome}>Natrag</button>
          </>
          : ''
      }</React.Fragment>
  )

}

export default ResultsComponent;